@use "sass:math";

////////////////////
// インナー幅設定
////////////////////
// コンテンツのインナー幅（最も共通の幅が多い部分）
$l-inner: 1100px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 20px;
// ※PC時：デザインカンプの余白の幅ではない。PC時は通常25pxで大丈夫！


////////////////////
// フォント設定
////////////////////

$base-font-family: "游ゴシック体", "Yu Gothic", YuGothic, "ヒラギノ角ゴ Pro", "Hiragino Kaku Gothic Pro", "メイリオ",
Meiryo, "MS Pゴシック", "MS PGothic", sans-serif;
$second-font-family: "游明朝体", "Yu Mincho", YuMincho, "ヒラギノ明朝 Pro", "Hiragino Mincho Pro", "MS P明朝", "MS PMincho", serif;
// ※検証ツールでしっかり反映されているか確認する

body {
  font-family: $base-font-family;
}

// フォントウェイト
$regular: 300;
$normal: 400;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$black: #111;
$white: #fff;
$blue:#3084BD;
$bg-blue:#0068AE;
$green:#52B6CF;
$letter:#0C68AE;


////////////////////
// メディアクエリとブレークポイントの設定
////////////////////

//ブレイクポイント
$breakpoints: (
  se: 320,
  x: 375,
  ssm: 426,
  sm: 600,
  md: 768,
  lg: 1024,
  lg02: 1290,
  xl: 1440,

);


//メディアクエリー
$mediaquerys: (
  se: "screen and (max-width: #{map-get($breakpoints,'se')}px)",
  x: "screen and (max-width: #{map-get($breakpoints,'x')}px)",
  ssm: "screen and (max-width: #{map-get($breakpoints,'ssm')}px)",
  sm: "screen and (max-width: #{map-get($breakpoints,'sm')}px)",
  md: "screen and (max-width: #{map-get($breakpoints,'md')}px)",
  lg: "screen and (max-width: #{map-get($breakpoints,'lg')}px)",
  lg02: "screen and (max-width: #{map-get($breakpoints,'lg02')}px)",
  xl: "screen and (max-width: #{map-get($breakpoints,'xl')}px)",
);

// @include mq(){}で書くとブレークポイントが反映される（初期値はmd）
@mixin mq($mediaquery: md) {
  @media #{map-get($mediaquerys, $mediaquery)} {
    @content;
  }
}

// remへの計算式（16pxを基準としている。10pxを基準とする場合は16を10に変更する）
@function rem($pixels) {
  @return math.div($pixels, 16) * 1rem;
}


////////////////////
// 非表示設定
////////////////////

//768px以上を表示
.sp {
  display: none;
  @include mq("sm") {
    display: block;
  }
}

//spのみ表示
.pc{
  @include mq("sm") {
    display: none;
  }
}
